<template>
	<div class="maps-hezi">
		<div id="maps" :style="style" ></div>
		<div id="r-result" v-if="searchResult.length">
			<ol>
				<li v-for="item in searchResult" @click="clickKeys(item)">{{item}}</li>
			</ol>
		</div>
		
	</div>

</template>
<script>
export default{
data(){
return{
style:{
width:'450px',
height:this.height+'px'
},
			searchResult:[]
}
},

props:{ //data
height:{
type:Number,
default:300
},
longitude:{}, //
latitude:{} //
},

	methods:{	
		creatmap(p1,p2){//
			var that=this;
window.map = new BMap.Map("maps");
var point = new BMap.Point(p1,p2);
map.centerAndZoom(point, 16);
			map.clearOverlays(); //
var marker = new BMap.Marker(point);// 
map.addOverlay(marker);// 
			marker.setAnimation(BMAP_ANIMATION_BOUNCE); //

			that.mapevent(map);//
		},
		mapevent(o){//
			var that=this;
			o.enableScrollWheelZoom();//
   			o.enableKeyboard();//

   			//
			var ctrl_nav = new BMap.NavigationControl({anchor:BMAP_ANCHOR_TOP_LEFT,type:BMAP_NAVIGATION_CONTROL_LARGE});
			o.addControl(ctrl_nav);
			//
			var ctrl_sca = new BMap.ScaleControl({anchor:BMAP_ANCHOR_BOTTOM_LEFT});
			o.addControl(ctrl_sca);

			//
			o.addEventListener("click",function(e){
				that.longitude=e.point.lng;
				that.latitude= e.point.lat;
				that.creatmap(that.longitude,that.latitude);//
				that.$emit("listenToChildEvent",e.point.lng , e.point.lat);//
			});
		},
		againmap(p1,p2,addre,key){//
			var that=this;
			
		    if(arguments.length==4){
		    	that.searchs(p1,p2,addre,key);//
		    }else{
		    	window.map = new BMap.Map("maps");
				var point = new BMap.Point(p1,p2);
			    map.centerAndZoom(point,16);
			    // ,
			    var myGeo = new BMap.Geocoder();
			    myGeo.getPoint(addre, function(point){
			        if (point) {
			            map.centerAndZoom(point, 16);
			            map.addOverlay(new BMap.Marker(point));
			            map.clearOverlays(); //
			            var marker = new BMap.Marker(point);  // 
			            map.addOverlay(marker);               // 
			            marker.setAnimation(BMAP_ANIMATION_BOUNCE); //
			            that.$emit("listenToChildEvent",point.lng, point.lat);//
			            that.mapevent(map);//
			        }else{
			            that.$message({
				          message: '',
				          type: 'warning'
				        });
			        }
			    });
		    }
		    
		},
		searchs(p1,p2,addre,key){
			var that=this;
			var options = {
				onSearchComplete: function(results){
					// 
					if (local.getStatus() == BMAP_STATUS_SUCCESS){
						var s = [];
						for (var i = 0; i < results.getCurrentNumPois(); i ++){
							s.push(results.getPoi(i).title + ", " + results.getPoi(i).address);
						}
						that.searchResult=s;
					}
				}
			};
			var local = new BMap.LocalSearch(map, options);
			local.search(key);

		},
		clickKeys(item){
			//this.searchResult=[];
			this.$emit("listenToSel",item);//
		},
		/**/
		clearKey(){
			this.searchResult=[];
		}
	},
	mounted:function() {
		this.creatmap(this.latitude,this.longitude);
	}
}
</script>
<style lang="scss">
/* <div class="maps-hezi">
		<div id="maps" :style="style" ></div>
		<div id="r-result" ></div>
	</div> */
.maps-hezi{
	position:relative;
	#r-result{
		position:absolute;
		top:-85px;
		background: #fff;
	    border: 1px solid rgb(131, 162, 165);
	    z-index: 999;
	    li{
	    	padding:0px 10px;
	    	cursor:pointer;
	    }
	    li:hover{
	    	background:#eeeeee;
	    }
	}
}
</style>



