<template>
	<section class="merchant-enter">
		<el-form :model="ruleForm" :rules="rules" ref="ruleForm">
			<el-button type="text" class="selector-btn" @click="selector('ruleForm')">{{shopHtml}}</el-button>
			<div v-if="isShop==1">				
				<category-bar :title="categoryBarTitle1"></category-bar>
				<el-form-item label="" prop="enterpriseName"  label-width="200px">
					<el-input :maxlength="30" v-model="ruleForm.enterpriseName" placeholder=""  class="wid400"></el-input>
				</el-form-item>
				<el-form-item label="/" label-width="200px">
					<upload-pictures :note="uploadTishi1"></upload-pictures>
				</el-form-item>
				<el-form-item label="" label-width="200px" prop="orgCode">
					<el-input :maxlength="18" v-model="ruleForm.orgCode" placeholder="" class="wid400"></el-input>
				</el-form-item>
				<!-- <el-form-item label="" label-width="200px" prop="orgCode">
					<el-input :maxlength="30" v-model = 'ruleForm.searcBarCode' placeholder=""  class="wid400"></el-input>
				</el-form-item> -->
			</div>

			<category-bar :title="categoryBarTitle2"></category-bar>
			<div v-if="isShop==1">
				<el-form-item label="" label-width="200px" prop="legalPerson">
					<el-input  :maxlength="30" v-model="ruleForm.legalPerson" placeholder="" class="wid400"></el-input>
				</el-form-item>
				<el-form-item label="" label-width="200px" prop="identityNumber">
					<el-input :maxlength="18" v-model="ruleForm.identityNumber" placeholder="" class="wid400"></el-input>
				</el-form-item>
			</div>
			<div v-else>
				<el-form-item label="" prop="contactMobile" label-width="200px">
					<el-input :maxlength="11" v-model="ruleForm.contactMobile" placeholder="" class="wid400"></el-input>
				</el-form-item>
				<el-form-item label="" label-width="200px" prop="contactName">
					<el-input :maxlength="30" v-model="ruleForm.contactName" placeholder="" class="wid400"></el-input>
				</el-form-item>

				
				<el-form-item label="" label-width="200px" prop="identityNumber">
					<el-input :maxlength="18" v-model="ruleForm.identityNumber"  placeholder="" class="wid400"></el-input>
				</el-form-item>
			</div>
			<el-form-item label="" label-width="200px">
				<upload-pictures :note="uploadTishi2"></upload-pictures>
				<div class="example">
					<div @click="iconSimple(src1)"><img src="../../assets/logo.png"/></div>
					<span></span>
				</div>
			</el-form-item>
			<el-form-item label="" label-width="200px">
				<upload-pictures :note="uploadTishi2"></upload-pictures>
				<div class="example">
					<div @click="iconSimple(src1)"><img src="../../assets/logo.png" /></div>
					<span></span>
				</div>
			</el-form-item>

			<div v-if="isShop==1">
				<category-bar :title="categoryBarTitle3"></category-bar>
				<el-form-item label="" prop="contactName" label-width="200px">
					<el-input :maxlength="30" v-model="ruleForm.contactName" placeholder="" class="wid400"></el-input>
				</el-form-item>
				<el-form-item  label="" prop="contactMobile" label-width="200px">
					<el-input  :maxlength="11" v-model="ruleForm.contactMobile" name="username" placeholder="" class="wid400"></el-input>
				</el-form-item>	
			</div>

			<category-bar :title="categoryBarTitle4"></category-bar>
			<el-form-item  label="" prop="name"  label-width="200px">
				<el-input :maxlength="20" v-model="ruleForm.name" placeholder=""  class="wid400"></el-input>
			</el-form-item>
			<el-form-item label=" "  prop="selIndustry" label-width="200px">
				<el-button class='selIndustryBtn' @click="selCategory"></el-button>
				<div class="myIndustry" v-if="ruleForm.selIndustry.length">
					<h4></h4>
					<ul>
						<li v-for="(item,index) in ruleForm.selIndustry">
							<b>{{item[0]}}>{{item[1]}}</b>
							<el-button type="text" @click="delIndustry(index)"></el-button>
						</li>
					</ul>
				</div>
				
			</el-form-item>
			
			<el-form-item label=""  label-width="200px">
				<v-distpicker :province="select.province" :city="select.city" :area="select.area" @province="onProvince" @city="onCity" @selected="onSelected"></v-distpicker>
			</el-form-item>
			<el-form-item label="" label-width="200px" prop="address" class="search-hezi">
				<el-input v-model="ruleForm.address" id="ser" placeholder="" class="wid280" @change="searchDetail" @focus="searchFocus"></el-input>
				<el-button type="primary" class="mapbtn" @click="searchbtn"></el-button>
				<ol class="search-ol" v-if="searchKeyArr.length">
					<li>123</li>
				</ol>
			</el-form-item>

			<el-form-item label="" label-width="200px">
				<p>{{ ruleForm.latitude+","+ruleForm.longitude}}</p>
			</el-form-item>
			<el-form-item label="" label-width="200px">
				<map-view :height="height" :longitude="ruleForm.longitude" :latitude="ruleForm.latitude" @listenToChildEvent="showsite" @listenToSel="showKey" ref="MapView">
				</map-view>
			</el-form-item>
			<el-form-item label="" label-width="200px">
				<el-button type="primary" @click="submitForm('ruleForm')"></el-button>
			</el-form-item>	
			 
		</el-form>

		<el-dialog title="" :visible.sync="dialogVisible" custom-class="big-img">
		    <img :src="exampleSrc"/>
		</el-dialog>

		<el-dialog
		    title=""
		    top="30%"
			:visible.sync="dialogVisible1"
			custom-class="suc"
			size="tiny">
			<div class="suc-string">
				<span class="el-icon-circle-check"></span>
				<div>
					<h2></h2>
			    	<p>1-5</p>
			    	<p></p>
				</div>
			</div>
		    
		    <span slot="footer" class="dialog-footer">
			    <el-button type="primary" @click="getBack"></el-button>
		    </span>
		</el-dialog>

		<el-dialog
	        title=""
	        top="30%"
	        :visible.sync="dialogVisible2"
	        custom-class="relevance-good"
	        size="tiny">
	      <div class="">
	        <el-row>
		      <el-col :span="8">
		        <category-menu title="" v-if="categoryData.length" :categoryData="categoryData" @categoryClick="firstHandle">
		        </category-menu>
		      </el-col>
		      <el-col :span="8">
		        <category-menu title="" v-if="secoundCategoryData.length" :categoryData="secoundCategoryData" @categoryClick="secondHandle"></category-menu>
		      </el-col>
		    </el-row>
		    <div class="category-nav-breadcrumb">
		      <span class="icon-arrow-top arrow-top-a"><span class="icon-arrow-top arrow-top-b"></span></span>
		      <el-breadcrumb separator=">">
		        <span class="breadcrumb-tips"></span>
		        <template v-if="curCateName.length" v-for="(item,index) in curCateName">
		        	<b v-if="index!=0" class="el-breadcrumb__separator">/</b>
		        	<span v-for="(value,index) in item">
		        		<b v-if="index==1" class="el-breadcrumb__separator">&gt;</b>
						<b>{{value}}</b>
		        	</span>
		        </template>
		      </el-breadcrumb>
		    </div>
	      </div>
	        
	        <span slot="footer" class="dialog-footer">
	          <el-button type="primary"  @click="industrySel"></el-button>
	          <el-button @click="dialogVisible2 = false"></el-button>
	        </span>
	    </el-dialog>
	   
	</section>
	
</template>

<script>
import CategoryBar from '@/components/CategoryBar.vue'/**/
import CategoryMenu from '@/components/CategoryMenu.vue'/**/
import UploadPictures from '@/components/UploadPictures.vue'/**/
import VDistpicker from 'v-distpicker';/**/
import MapView from '@/components/Map';/**/
import { merchantSave,industryListall,gssUpload } from '@/api/shopApi';
// 
/*Vue.filter('onlyNumber', function (value) {
  return value.replace(/[^/d]/g,''); 
});*/
export default {
	components: {
		CategoryBar,
		CategoryMenu,
		UploadPictures,
		VDistpicker,
		MapView	
	},
	data() {
		var validatePhone = (rule, value, callback) => {
	        if (value === '') {
	          callback(new Error(''));
	        } else {
	        	var first=value.slice(0,1);
		        if ((value.length!=11)||(first!=1)) {
		          	callback(new Error(''));
		        }else{
		        	callback();
		        }
	        }
	    };
		return {
			
			//
			curCateName:[],
			stCateName:'',
			categoryData: [],
			industryCateId:[],
			industryCateIdList:[],
            secoundCategoryData:[],

          	dialogVisible2:false,

			dialogVisible1:false,
			height:300,
			selCity:"",
			selArea:'',
			selProvince:'',
			sel:'',
			isAdd:1,
			select: { 
				province: '',
			    city: '',
			    area: ''
			},
			isShop:1,
			shopHtml:'',
			categoryBarTitle1: '',
			categoryBarTitle2: '',
			categoryBarTitle3: '',
			categoryBarTitle4: '',
			uploadTishi1:"800*800pxjpgjpegpng,5MB",
			uploadTishi2:"",
			exampleSrc:'',
			dialogVisible:false,
			src1:"http://2.hapn.cc:20080/n/00802v_b009W1MMTZ0a02w8.jpg",

			searchsite:"",
			selcity:"",
			listLoading:false,
			searchKeyArr:[],

			ruleForm: {
				searcBarCode:'',
				enterpriseName:'',
				orgCode: '',
				legalPerson: '',
				identityNumber: '',
				name: '',
				selIndustry:[],
				detailedAddress: '',
				contactName:'',
			    contactMobile:'',
			    enterpriseLicence:'',
			    identityPic:'',
			    longitude:120.186535,
		latitude:30.310288,
			    address:''
				
			},
	        rules: {
	          	enterpriseName: [
	            	{ required: true, message: '', trigger: 'blur' },
	            	{ min: 1, max: 30, message: ' 1  30 ', trigger: 'blur' }
	          	],
	          	orgCode: [
	            	{ required: true, message: '/', trigger: 'blur' },
	            	{ min: 18, max: 18, message: '', trigger: 'blur' }
	          	],
	          	legalPerson: [
	            	{ required: true, message: '', trigger: 'blur' },
	            	{ min: 2, max: 30, message: '', trigger: 'blur' }
	          	],
		        identityNumber: [
		            { required: true, message: '', trigger: 'blur' },
	            	{ min: 18, max: 18, message: '', trigger: 'blur' }
		        ],
	          	name: [
	            	{ required: true, message: '', trigger: 'blur' },
	            	{ min: 1, max: 20, message: ' 1  20 ', trigger: 'blur' }
	          	],
	          	/*selIndustry:[
	          		{ required: true, message: '', trigger: 'change' },
	          	],*/
	          	detailedAddress:[
	          		{ required: true, message: '', trigger: 'blur' },
	          	],
	          	contactName: [
	            	{ required: true, message: '', trigger: 'blur' },
	            	{ min: 2, max: 30, message: '', trigger: 'blur' }
	          	],
		        contactMobile: [
		        	{ validator: validatePhone,trigger: 'blur' }
	          	],
		        address:[
		           { required: true, message: '', trigger: 'blur' }
		        ]
	          	
	        }

		}
	},
/*	watch:{
		'ruleForm.searcBarCode':function(){
			
            this.ruleForm.searcBarCode=this.ruleForm.searcBarCode.replace(/\W/g,'');
            var newVal=this.ruleForm.searcBarCode;
            console.log(newVal)
            this.$set(this.ruleForm,'searcBarCode',newVal);
            console.log(this.ruleForm.searcBarCode)
        },

		'ruleForm.orgCode':function(){
			this.ruleForm.orgCode=this.ruleForm.orgCode.replace(/[\d]/g,'');
			console.log(this.ruleForm.orgCode)
		}
		'ruleForm.orgCode':function(){
            this.ruleForm.orgCode=this.ruleForm.orgCode.replace(/\D/g,'');
            console.log(this.ruleForm.orgCode)
        }
	},*/
    mounted:function(){
		//
	    this.$refs.MapView.creatmap(this.ruleForm.longitude,this.ruleForm.latitude);

	    //
	    industryListall({}).then((res) => {
	        this.categoryData = res.data.data;
	    });

	    var that=this;
	    window.document.onclick=function(){
	    	that.$refs.MapView.clearKey();
	    }

    },
	methods: {
		//input
		showKey(key){
			this.ruleForm.address=key;
		},
		searchFocus(){
			var addr=this.selProvince+this.selCity+this.selArea+this.ruleForm.address;
			if(!addr){
				this.$message({
		          message: '',
		          type: 'warning'
		        });
			}
		},
		//
		searchDetail(){
			var addr=this.selProvince+this.selCity+this.selArea+this.ruleForm.address;
			this.$refs.MapView.againmap(this.ruleForm.longitude,this.ruleForm.latitude,addr,this.ruleForm.address);

		},
		/*filterNumber(value){
			this.ruleForm.orgCode=this.ruleForm.orgCode.replace(/[^/d]/g,'');
		},*/
		//
		selCategory(){
			this.dialogVisible2=true;
			this.curCateName=[];
			this.secoundCategoryData=[];
		},
		//
		firstHandle (row, index){

			this.stCateName='';
			this.stCateName=row.industryCateName;
	        if(row.secondIndustryList.length) {
	            this.secoundCategoryData = row.secondIndustryList;
	        }else {
	            this.secoundCategoryData = [];
	        }
        },
        //
        secondHandle (row, index) {
        	var maxArr=this.ruleForm.selIndustry.concat(this.curCateName);
   			this.industryCateId.push(row.industryCateId);
        	if(maxArr.length>9){
        		this.$message({
		          message: '10',
		          type: 'warning'
		        });
		        return false;
        	}

        	this.curCateName.push([this.stCateName,row.industryCateName]);


        },
        //
        industrySel(){
        	if (this.curCateName.length==0) {
        		return false;
        	}else{
        		this.ruleForm.selIndustry=this.ruleForm.selIndustry.concat(this.curCateName);
        		this.industryCateIdList=this.industryCateIdList.concat(this.industryCateId);
				
        		this.dialogVisible2=false;
        	}
        },
        //
        delIndustry(index){
        	Array.prototype.baoremove = function(dx) 
			{ 
			  if(isNaN(dx)||dx>this.length){return false;} 
			  this.splice(dx,1); 
			} 
        	this.ruleForm.selIndustry.baoremove(index);
        	this.industryCateIdList.baoremove(index);
        },
		/**/
		iconSimple(src){
			this.exampleSrc=src;
			this.dialogVisible=true;
		},

		/**/
		onProvince(data) {
		    this.select.province=data.value;
		    if(data.code){
		    	this.selProvince=data.value;
		    }else{
		    	this.selProvince='';
		    }
		    this.selCity='';
		    this.selArea="";
	    },
	    onCity(data){
	    	this.select.city=data.value;
	    	if(data.code){
		    	this.selCity=data.value;
		    }else{
		    	this.selCity='';
		    }
		    this.selArea="";
	    },
	    onSelected(data){
	    	this.select.area=data.area.value;
	    	if(data.area.code){
		    	this.selArea=data.area.value;
		    }else{
		    	this.selArea='';
		    }
	    },
	    /*map*/
		searchbtn(){
			var addr=this.selProvince+this.selCity+this.selArea+this.ruleForm.address
			this.$refs.MapView.againmap(this.ruleForm.longitude,this.ruleForm.latitude,addr);
		},
		/**/
		showsite(lng,lat){
			this.ruleForm.longitude=lng;
			this.ruleForm.latitude=lat;
		},
		/**/
		selector(formName){
			this.$refs[formName].resetFields();/**/
			this.select= { province: '',city: '',area: ''};
			if(this.isShop==1){/*isShop1*/
				this.isShop=0;
				this.shopHtml="";
				
			}else{
				this.isShop=1;
				this.shopHtml="";
			}
		},
		/**/
		submitForm(formName) {
		    this.$refs[formName].validate((valid) => {
	            if (valid) {
		      		if(!this.selProvince){//
						this.$message({
				          message: '',
				          type: 'warning'
				        });
				        return false;
					}else{//
						this.sel=this.selProvince;
						if(this.selCity){
							this.sel+="*"+this.selCity;
						}
						if(this.selArea){
							this.sel+="*"+this.selArea;
						}
					}
		        	this.listLoading = true;
		            var para = new URLSearchParams();
		            para.append('userId',userId);
		            para.append('enterpriseName',this.ruleForm.enterpriseName);
		            para.append('enterpriseLicence',this.ruleForm.enterpriseLicence);
		            para.append('orgCode',this.ruleForm.orgCode);
		            para.append('legalPerson',this.ruleForm.legalPerson);
		            para.append('identityNumber',this.ruleForm.identityNumber);
		            para.append('identityPic',this.ruleForm.identityPic);		          
		            para.append('contactName',this.ruleForm.contactName);
		            para.append('contactMobile',this.ruleForm.contactMobile);
		            para.append('name',this.ruleForm.name);
		            para.append('industryCateIdList',this.industryCateIdList);
		            para.append('address',this.ruleForm.address);
		            para.append('longitude',Number(this.ruleForm.longitude*1000000));
					para.append('latitude',Number(this.ruleForm.latitude*1000000));

		            merchantSave(para).then((res)=> {
		            	this.listLoading = false;
		            	if(res.data.code==0){
		            		this.dialogVisible1=true;
		            	}
		                
		            }).catch((res)=> {
		                this.listLoading = false;
		            });
		      	}else {
		        	return false;
		      	}
		    });
		},
		/**/
	    getBack(){
	    	this.dialogVisible1 = false;
	    	this.$router.push({ path: '/seller-management/goods' });
	    }

	}
}
</script>
<style lang="scss">
.search-hezi{
	position:relative;
	.search-ol{
		border:1px solid rgb(191, 216, 217);
		position:absolute;
		top:36px;
		width:280px;
		background:#fff;
		z-index:999;
		padding-left:10px;
	}
}
</style>



